@import url(https://fonts.googleapis.com/css?family=PT+Serif&display=swap);
@import url(https://fonts.googleapis.com/css?family=Oxanium:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  margin: 1;
  box-sizing: border-box;
}

body {
  font-size: 92%;
  font-family: 'PT Serif', serif;
  color: white;
  background: -webkit-gradient(linear,left top, right top,from(#000428),to(#004e92));
  background: linear-gradient(to right,#000428,#004e92)
}
h2, h3, h4, h5 {
  margin: 0 0 3px;
}


.listOfCards {
  display: inline;
  flex-wrap: wrap;
}

.UserCard {  
  display: inline-block;
  width: 290px;
  color:black;
  font-family: 'PT Serif', serif;
  height: 360px;
  border-radius: 10px;
  box-shadow: 5px 5px 30px black;
  margin: 10px;
  background: #0c2b5e; 
  background: -webkit-gradient(linear,left top, left bottom,from(#8e9eab),to(#eef2f3)); 
  background: linear-gradient(#8e9eab,#eef2f3)
}

.UserCard img {

  width: 200px;
  height: 200px;
  margin: 20px 0 0 0;
  border-radius: 50%;
  border: 4px solid black;
  object-fit: cover;
}

.UserCardTop {
  height: 67%;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.UserCardBottom {
  min-height: 40%;
  overflow: auto;
  min-width: 100%;
  max-width: 100%;
  padding: 0 10px 5px;
  overflow-wrap: break-word;
}

.UserCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
.UserCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
.UserCardBottom::-webkit-scrollbar-thumb {
  background-color: #bec4c8; 
  border-radius:3px;
}


body, html {
    height: 100vh;
    text-align: center;
 
    margin: 0;
    scroll-behavior: smooth;
}

.header-image {
    height: 100vh;
    
}

.header-text {
    text-align: center;
    position: absolute;
    font-size: 300%;
    top: 50%;
    left:50%;
    font-family: 'Oxanium';
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    color: black;
}

.subhead {
    font-size: 300%;
    color: white;
    font-family: 'Oxanium';
}

.listOfCards {
    display: inline;
    flex-wrap: wrap;
  }
  
  .ProjectCard {  
    display: inline-block;
    width: 452px;
    color: black;
    height: 300px;
    border-radius: 10px;
    box-shadow: 5px 5px 30px black;
    margin: 10px;
    background: -webkit-gradient(linear,left top, right top,from(#8e9eab),to(#eef2f3));
    background: linear-gradient(to right,#8e9eab,#eef2f3)
  }
  
  .ProjectCard img {
    float: left;
    width: 226px;
    height: 290px;
    margin-top: 5px;
    border-radius: 10%;
    padding: 1em;
    object-fit: cover;
  }
  
  .ProjectCardTop {
    height: 22%;
    overflow: visible;
  }
  

  .ProjectCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
  .ProjectCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
  .ProjectCardBottom::-webkit-scrollbar-thumb {
    background-color: #bec4c8; 
    border-radius:3px;
  }
  
 

  .topnav {
    
